<template>
  <div id="firstall">
    <div id="protect" v-if="animatefalse">
      ...
      </div>
    <div id="firsttop">
      <div id="firstchartfirst">
        <canvas ref="chartCanvas"></canvas>
      </div>
      <ul>
        <li :class="{'animate__fadeInLeft animate__animated animate__bounce animate__delay-0.2s':animatefalse}">
          <img src="/src/3.png" alt="" class="firsttoppic">
          <p>{{historysum[0]}}</p>
        </li>
        <li :class="{'animate__fadeInRight animate__animated animate__bounce animate__delay-0.4s':animatefalse}">
          <img src="/src/4.png" alt="" class="firsttoppic">
          <p>{{historysum[1]}}</p>
        </li>
        <li :class="{'animate__fadeInUp animate__animated animate__bounce animate__delay-0.6s':animatefalse}">
          <img src="/src/5.png" alt="" class="firsttoppic">
          <p>{{historysum[2]}}</p>
        </li>
        <li :class="{'animate__fadeInDown animate__animated animate__bounce animate__delay-0.8s':animatefalse}">
          <img src="/src/6.png" alt="" class="firsttoppic">
          <p>{{historysum[3]}}</p>
        </li>
      </ul>
    </div>
    <div id="firstchartsecond">
      <div id="firstpiechart">
        <canvas ref="chartPie"></canvas>
      </div>
      <div id="firsttimechart">
        <canvas ref="chartline"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, nextTick,reactive } from 'vue';
import Chart from 'chart.js/auto';
import 'animate.css'
export default {
  props:['alldata'],
  setup(props) {
    var animatefalse=ref(true)
    setTimeout(() => {
      animatefalse.value=false
    }, 1050);
    const chartCanvas = ref(null);
    const chartPie = ref(null);
    const chartline=ref(null)

    let firstchartfirst = null;
    let firstchartpie = null;
    let firstcharttime=null
    // 
    const today = new Date();
    const nowaday = new Date(today.getFullYear(), today.getMonth(), today.getDate());

    //  reactive 
    var indicatorsum = reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].indicator.length).fill(0));
    var moodsum = reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].mood.length).fill(0));
    var historysum = reactive(new Array(props.alldata[nowaday.getFullYear()][nowaday.getMonth()][nowaday.getDate()].history.length).fill(0));
    var moodline = reactive({
      'happy': [],
      'sad': [],
      'anger': [],
      'surprise': [],
      'hate': [],
      'fear': [],
      'complex': []
    });

    // 
    function getPreviousDaysData() {
      const dates = [];
      for (let i = 0; i < 7; i++) {
        const date = new Date(today - i * 24 * 60 * 60 * 1000);
        dates.push(date);
      }
      return dates;
    }

    const previousDays = getPreviousDaysData();

    function indicatorok() {
      var totalindicator = 0;
      previousDays.forEach(date => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const day = date.getDate();
        if (props.alldata[year] && props.alldata[year][month] && props.alldata[year][month][day]) {
          const dailyindicators = props.alldata[year][month][day].indicator;
          dailyindicators.forEach((value, index) => {
            indicatorsum[index] += value;
          });
          totalindicator++;
        }
      });
      indicatorsum.forEach((el, index) => {
        indicatorsum[index] /= totalindicator;
      });
    }

    function moodok() {
      var totalmood = 0;
      previousDays.forEach(date => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const day = date.getDate();
        if (props.alldata[year] && props.alldata[year][month] && props.alldata[year][month][day]) {
          const dailymoods = props.alldata[year][month][day].mood;
          dailymoods.forEach((value, index) => {
            moodsum[index] += value;
          });
          totalmood++;
        }
      });
      moodsum.forEach((el, index) => {
        moodsum[index] /= totalmood;
      });
    }

    function historyok() {
      previousDays.forEach(date => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const day = date.getDate();
        if (props.alldata[year] && props.alldata[year][month] && props.alldata[year][month][day]) {
          const dailyhistory = props.alldata[year][month][day].history;
          dailyhistory.forEach((value, index) => {
            historysum[index] += value;
          });
        }
      });
    }

    function lineofmood() {
      previousDays.forEach(date => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const day = date.getDate();
        if (props.alldata[year] && props.alldata[year][month] && props.alldata[year][month][day]) {
          const dailymood = props.alldata[year][month][day].mood;
          moodline['happy'].push(dailymood[0]);
          moodline['sad'].push(dailymood[1]);
          moodline['anger'].push(dailymood[2]);
          moodline['surprise'].push(dailymood[3]);
          moodline['hate'].push(dailymood[4]);
          moodline['fear'].push(dailymood[5]);
          moodline['complex'].push(dailymood[6]);
        }
      });
    }

    // 
    moodok();
    indicatorok();
    historyok();
    lineofmood();











    onMounted(async () => {
      await nextTick();

      // ---  ---
      if (chartCanvas.value) {
        const ctx = chartCanvas.value.getContext('2d');
        firstchartfirst = new Chart(ctx, {
          type: 'bar',
          data: {
            labels: [
              '',
              '',
              '',
              ''
            ],
            datasets: [{
              label: '',
              data: indicatorsum,
              backgroundColor: ['#4CAF50', '#2196F3', '#FF9800', '#9C27B0'],
              borderRadius: 6,
              barThickness: 24
            }]
          },
          options: {
            responsive: true,
            indexAxis: 'y',
            plugins: {
              legend: { display: false },
              title: { display: true, text: '', font: { size: 18 } },
              tooltip: {
                callbacks: {
                  label: (context) => `${context.label}: ${context.raw} `
                }
              }
            },
            scales: {
              x: {
                min: 0, 
                title: { display: true, text: '' },
                ticks: { callback: (value) => value}
              }
            }
          }
        });
      }

      // ---  ---
      if (chartPie.value) {
        const ctx2 = chartPie.value.getContext('2d');
        const emotionDescriptions = {
          '': '',
          '': '',
          '': '',
          '': '',
          '': '',
          '':'',
          '':'',
          '':''
        };
        firstchartpie = new Chart(ctx2, {
          type: 'pie',
          data: {
            labels: ['', '', '', '', '','',''],
            datasets: [{
              label: '',
              data: moodsum,
              backgroundColor: ['#228B22', '#191970', '#DC143C', '#9932CC', '#A0522D','#DCDCDC','#B0C4DE'],
              borderWidth: 1
            }]
          },
          options: {
            responsive: true,
            plugins: {
              tooltip: {
                callbacks: {
                  label: (context) => {
                    const label = context.label;
                    const value = context.raw;
                    const description = emotionDescriptions[label] || '';
                    return `${label}: ${value}%\n${description}`;
                  }
                }
              },
              legend: { position: 'bottom' },
              title: { display: true, text: '', font: { size: 18 } }
            }
          }
        });
      }

      if(chartline.value){
        let ctx3 = chartline.value.getContext('2d');
        firstcharttime = new Chart(ctx3, {
          type: 'line',
          data: {
            // labels: ['', '', '', '', '', '', ''],
            labels: ['', '', '', '', '', '', ''],
            datasets: [
          { label: '', data: moodline['happy'], borderColor: '#228B22', backgroundColor: 'transparent', tension: .4 },
          { label: '', data: moodline['sad'], borderColor: '#191970', backgroundColor: 'transparent', tension: .4 },
          { label: '', data: moodline['anger'], borderColor: '#DC143C', backgroundColor: 'transparent', tension: .4 },
          { label: '', data: moodline['surprise'], borderColor: '#9932CC', backgroundColor: 'transparent', tension: .4 },
          { label: '', data: moodline['hate'], borderColor: '#A0522D', backgroundColor: 'transparent', tension: .4 },
          { label: '', data: moodline['fear'], borderColor: '#DCDCDC', backgroundColor: 'transparent', tension: .4 },
          { label: '', data: moodline['complex'], borderColor: '#B0C4DE', backgroundColor: 'transparent', tension: .4 }
        ]
      },
          options: {
            responsive: true,
            maintainAspectRatio: false,
            interaction: { mode: 'index', intersect: false },
            plugins: {
              legend: { display: false },
              tooltip: {
                callbacks: {
                  title: t => t[0].label
                }
              },
              title: { display: true, text: '', font: { size: 18 } }
            },
            scales: {
              y: { beginAtZero: true,  ticks: { stepSize: 20 } },
              x: { grid: { display: false } }
            }
          }
        });
      }
    });

    return { chartCanvas, chartPie,animatefalse,chartline,historysum };
  }
};
</script>



<style scoped>
 
#protect{
  width: 100%;
  height: 100%;
  background-color: #73737356;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  font-weight: 640;
  font-family: 'KaiTi';
  color:white;
  z-index:10;
  position: absolute;
  
}
#firstall{
  width: 100%;
  height: 100%;
  transform: translate(0,-10%);
  background-color: #ffffff61;
}
#firsttop{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-evenly;
}
#firstchartfirst{
  width: 60%;
  height: 100%;
}

ul{
  background-color: #ffffff77;
  width: 30%;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;;
}
li{
  width: 100%;
  height: 25%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
li:hover{
  box-shadow: 0 0 3px 0 black;
  background-color: #51ffc5bd;
  animation:hoverup 0.2s ease
}
.firsttoppic{
  width: 18%;
  height: 80%;
}
p{
  font-size: 20px;
  font-weight: 550;
}
@keyframes hoverup {
  0%{
    transform: translate(0,0);
  }
  50%{
    transform: translate(0,-10%);
  }
  100%{
    transform: translate(0,0);
  }
}
#firstchartsecond{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: space-evenly;

}
#firstpiechart{
  width: 30%;
  height: 100%;
}
#firsttimechart{
  width: 60%;
  height: 100%;
}
#firstchartfirst:hover,#firstpiechart:hover,#firsttimechart:hover{
  background-color: #fff;
}
@keyframes setup {
  0%{
    transform: translate(0,20%);
    opacity: 0.2;
  }
  100%{
    transform: translate(0,0);
    opacity: 1;
  }
}
#firstchartfirst,#firstpiechart,#firsttimechart{
  animation:setup 0.8s ease;
}
</style>