<template>
  <div id="soundre">
    <br><hr>
    <h1></h1>
    <br><hr>
    <p>{{ picre[props.result.data.trim()] }}</p>
  </div>
</template>

<script>
import { eventBus } from '@/eventbus'
export default {
  props:['result'],
  setup(props){
    const nowaday=new Date()
    const picdatachange={
      '':0,
      '':1,
      '':2,
      '':3,
      '':4,
      '':5,
      '':6
    }
    const picre={
      '':'',
      '':'',
      '':'',
      '':'',
      '':'',
      '':'',
      '':''
    }
    console.log("data",props.result.data)
    eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:0,time:1,action:'<p class=actionmain><strong>'+props.result.data+'</strong></p>'})
    eventBus.emit('changemoodhistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:picdatachange[props.result.data.trim()],time:1,})
    // eventBus.emit('actionhistory',{action:`<p class=actionmain><strong>${picre[props.result.data.trim()]}</strong></p>`})
    return {
      props,picre
    }
  }
}
</script>

<style scoped>

#soundre{
  width: 100%;
  height: 100%;
}
#soundre:hover{
  background-color: #ffffff9d;
}
p{
  font-size: 50px;
  font-weight: 650;
  width: 70%;
  height: 60%;
  font-family: 'KaiTi';
  margin:5% auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>