<template>
 <div id="thirdtimeassistant" class="thirdcontainer">
    <!--  -->
    <div id="thirdtoprow" class="thirdtoprow">
      <button class="thirtopaudio" @click="thirdPlayAudio('wind')"></button>
      <button class="thirtopaudio" @click="thirdPlayAudio('rain')"></button>
      <button class="thirtopaudio" @click="thirdPlayAudio('bird')"></button>
      <button class="thirtopaudio" @click="thirdPlayAudio('stream')"></button>
    </div>

    <!--  -->
    <div id="thirdmain" class="thirdmain">
      <!--  -->
      <div id="thirdleftbar" class="thirdleftbar">
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(1)">1</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(5)">5</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(30)">30</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(-1)">1</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(-5)">5</button>
        <button class="thirdctrlbtn" @click="thirdAdjustMinutes(-30)">30</button>
        <button class="thirdctrlbtn" @click="thirdAddWaterReminder"></button>
        <button class="thirdctrlbtn" @click="thirdSetRestPoint"></button>
        <button class="thirdctrlbtn" @click="thirdSwitchToRelief"></button>
      </div>

      <!--  v-if  -->
      <div id="thirdrightarea" class="thirdrightarea">
        <!--  -->
        <div v-if="thirdActiveView === 'timer'" id="thirdtimerview" class="thirdtimerview">
          <!-- 85%50% -->
          <div id="thirdtimedisplaywrap" class="thirdtimedisplaywrap">
            <div id="thirdtimedisplay" class="thirdtimedisplay">
              {{ thirdDisplayTime }}
            </div>
          </div>

          <!-- 90% -->
          <div id="thirdprogresswrap" class="thirdprogresswrap">
            <div class="thirdprogressbar">
              <div
                class="thirdprogressinner"
                :style="{
                  width: thirdProgressPercent + '%',
                  backgroundColor: thirdProgressColor
                }"
              ></div>
            </div>

            <!--  -->
            <div class="thirdmarkerswrap">
              <div
                v-for="(m, idx) in thirdMarkers"
                :key="idx"
                class="thirdmarker"
                :style="{ backgroundColor: m.color, left: (m.minute / 120 * 100) + '%' }"
                :title="m.type === 'water' ? ': ' + m.minute + ' ' : ': ' + m.minute + ' '"
              ></div>
            </div>
          </div>

          <!--  -->
          <div id="thirdcontrols" class="thirdcontrols">
            <button class="thirdactionbtn" @click="thirdStart"></button>
            <button class="thirdactionbtn" @click="thirdPause"></button>
            <button class="thirdactionbtn" @click="thirdImmediateEnd"></button>
          </div>
        </div>

        <!--  -->
        <div v-if="thirdActiveView === 'relief'" id="thirdreliefview" class="thirdreliefview">
          <div class="thirdreliefgrid">
            <div class="thirdreliefcard" v-for="(item, idx) in thirdReliefItems" :key="idx">
              <img :src="item.img" alt="" class="thirdreliefimg" />
              <div class="thirdreliefbody">
                <h3 class="thirdrelieftitle">{{ item.title }}</h3>
                <p class="thirdreliefdesc">{{ item.desc }}</p>
                <a class="thirdrelieflink" :href="item.link" target="_blank"></a>
              </div>
            </div>
          </div>

          <div class="thirdbackbtnwrap">
            <button class="thirdactionbtn" @click="thirdSwitchToTimer"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from "@/eventbus";
import { ref, reactive, computed, onUnmounted } from "vue";

export default {
  name: "ThirdTimeAssistant",
   setup() {
    // ----------  ----------
    const thirdAudioPaths = reactive({
      wind: "/src/68.m4a", 
      rain: "/src/69.m4a",
      bird: "/src/70.m4a",
      stream: "/src/71.m4a"
    });

    let thirdAudioObject = null;
    let audionum=0
    const thirdPlayAudio = (which) => {
      const whichname={
        'wind':'',
        'rain':'',
        'bird':'',
        'stream':''
      }
      // 
      console.log("",audionum)
      try {
        if (thirdAudioObject&&audionum%2!==0) {
          thirdAudioObject.pause();
          thirdAudioObject.currentTime = 0;
          eventBus.emit('information','')
          audionum++
          return
        }
      } catch (e) { /* ignore */ }

      // 
      if (thirdAudioPaths[which]&&audionum%2===0) {
        thirdAudioObject = new Audio(thirdAudioPaths[which]);
        thirdAudioObject.loop = true;
        audionum++
        eventBus.emit('information',`${whichname[which]}`)
        eventBus.emit('changeaction',{action:`<p id=actionmain><strong></strong><strong>${whichname[which]}</strong></p>`})
        thirdAudioObject.play().catch((e) => {
          console.warn("", e);
        });
      } else {
        // 
        console.log("", which);
      }
    };

    // ----------  state ----------
    const thirdConfiguredMinutes = ref(0); // 0~120
    const thirdRemainingSeconds = ref(0); // 
    const thirdTimerInterval = ref(null);
    const thirdIsRunning = ref(false);

    // markers: { type: 'water'|'rest', minute: Number, color: String, triggered: Boolean }
    const thirdMarkers = ref([]);

    // 
    const presetColors = [
      "#ff7f7f", "#7fbfff", "#7fffbf", "#ffd27f", "#c57fff", "#7fffff", "#ffb3ff"
    ];
    const thirdUsedColors = ref([]);
    const thirdProgressColor = ref(presetColors[0]);

    // 
    const thirdActiveView = ref("timer"); // 'timer'  'relief'

    // 
    const thirdReliefItems = ref([
      { title: "", desc: "", img: "/src/65.png", link: "https://emotioncompass.org/zh/" },
      { title: "", desc: "", img: "/src/66.png", link: "http://psy.china.com.cn/2023-06/20/content_42329815.htm" },
      { title: "FeelGoodPal", desc: "", img: "/src/67.png", link: "https://feelgoodpal.com/zh/blog/ways-to-relieve-stress-and-anxiety/" }
    ]);

    // ----------  ----------
    const thirdProgressPercent = computed(() => {
      return (thirdConfiguredMinutes.value / 120) * 100;
    });

    // ----------  mm:ss ----------
    const formatTime = (sec) => {
      const s = Math.max(0, Math.floor(sec));
      const mm = Math.floor(s / 60)
        .toString()
        .padStart(2, "0");
      const ss = (s % 60).toString().padStart(2, "0");
      return `${mm}:${ss}`;
    };

    const thirdDisplayTime = computed(() => {
      //  00:00
      if (!thirdRemainingSeconds.value && !thirdConfiguredMinutes.value) {
        return "00:00";
      }
      // :00
      if (!thirdIsRunning.value && thirdConfiguredMinutes.value) {
        return `${String(thirdConfiguredMinutes.value).padStart(2, "0")}:00`;
      }
      return formatTime(thirdRemainingSeconds.value);
    });

    // ----------  0~120 ----------
    const thirdAdjustMinutes = (delta) => {
      const prior = thirdConfiguredMinutes.value;
      let newVal = prior + delta;
      if (newVal > 120) newVal = 120;
      if (newVal < 0) newVal = 0;

      // 
      const diff = newVal - prior;
      thirdConfiguredMinutes.value = newVal;

      //  remaining
      if (thirdIsRunning.value && diff !== 0) {
        thirdRemainingSeconds.value = Math.max(0, thirdRemainingSeconds.value + diff * 60);
      } else if (!thirdIsRunning.value) {
        //  remaining  0 configured
        thirdRemainingSeconds.value = newVal > 0 ? newVal * 60 : 0;
      }
    };

    // ----------  ----------
    const thirdPickNewColor = () => {
      for (const c of presetColors) {
        if (!thirdUsedColors.value.includes(c)) {
          thirdUsedColors.value.push(c);
          return c;
        }
      }
      // 
      let candidate;
      do {
        candidate = "#" + Math.floor(Math.random() * 16777215).toString(16).padStart(6, "0");
      } while (thirdUsedColors.value.includes(candidate));
      thirdUsedColors.value.push(candidate);
      return candidate;
    };

    // ----------  ----------
    const thirdAddWaterReminder = () => {
      //  marker  minute 
      const minute = Math.max(0, Math.min(120, Math.ceil(thirdConfiguredMinutes.value)));
      const color = thirdPickNewColor();
      thirdMarkers.value.push({
        type: "water",
        minute,
        color,
        triggered: false
      });
      // 
      thirdProgressColor.value = color;
      console.log("minute=", minute);
    };

    // ----------  ----------
    const thirdSetRestPoint = () => {
      const minute = Math.max(0, Math.min(120, Math.ceil(thirdConfiguredMinutes.value)));
      const color = thirdPickNewColor();
      thirdMarkers.value.push({
        type: "rest",
        minute,
        color,
        triggered: false
      });
      thirdProgressColor.value = color;
      console.log("minute=", minute);
    };

    // ----------  console.log ----------
    const thirdHandleMarkerTriggered = (marker) => {
      if (marker.type === "water") {
        console.log("");
      } else if (marker.type === "rest") {
        console.log("");
      }
      marker.triggered = true;
    };

    // ----------  ----------
    const thirdStart = () => {
      // 
      if (thirdConfiguredMinutes.value <= 0 && thirdRemainingSeconds.value <= 0) return;

      //  remaining configured 
      if (!thirdRemainingSeconds.value) {
        thirdRemainingSeconds.value = thirdConfiguredMinutes.value * 60;
      }

      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
        thirdTimerInterval.value = null;
      }
      eventBus.emit("action",{action:"<p id=actionmain><strong></strong></p>"})
      thirdIsRunning.value = true;
      thirdTimerInterval.value = setInterval(() => {
        if (thirdRemainingSeconds.value > 0) {
          thirdRemainingSeconds.value -= 1;

          // 
          const remainingMinutesCeil = Math.ceil(thirdRemainingSeconds.value / 60);
          thirdMarkers.value.forEach((m) => {
            if (!m.triggered && remainingMinutesCeil === m.minute) {
              thirdHandleMarkerTriggered(m);
            }
          });
        } else {
          // 
          clearInterval(thirdTimerInterval.value);
          thirdTimerInterval.value = null;
          thirdIsRunning.value = false;
          // 
          console.log("");
        }
      }, 1000);
    };

    // ----------  ----------
    const thirdPause = () => {
      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
        thirdTimerInterval.value = null;
      }
      thirdIsRunning.value = false;
    };

    // ----------  ----------
    const thirdImmediateEnd = () => {
      if (thirdRemainingSeconds.value > 0) {
        const ok = confirm("");
        if (!ok) return;
      }
      // 
      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
        thirdTimerInterval.value = null;
      }
      thirdIsRunning.value = false;
      thirdRemainingSeconds.value = 0;
      thirdConfiguredMinutes.value = 0;
      //  markers & colors
      thirdMarkers.value = [];
      thirdUsedColors.value = [];
      thirdProgressColor.value = presetColors[0];
    };

    // ----------  ----------
    const thirdSwitchToRelief = () => {
      thirdActiveView.value = "relief";
    };
    const thirdSwitchToTimer = () => {
      thirdActiveView.value = "timer";
    };

    // ----------  interval ----------
    onUnmounted(() => {
      if (thirdTimerInterval.value) {
        clearInterval(thirdTimerInterval.value);
      }
      if (thirdAudioObject) {
        try {
          thirdAudioObject.pause();
          thirdAudioObject = null;
        } catch (e) {}
      }
    });

    // ----------  ----------
    return {
      // audio
      thirdPlayAudio,
      // states
      thirdConfiguredMinutes,
      thirdRemainingSeconds,
      thirdMarkers,
      thirdProgressPercent,
      thirdProgressColor,
      thirdDisplayTime,
      thirdIsRunning,
      thirdActiveView,
      thirdReliefItems,
      // actions
      thirdAdjustMinutes,
      thirdAddWaterReminder,
      thirdSetRestPoint,
      thirdStart,
      thirdPause,
      thirdImmediateEnd,
      thirdSwitchToRelief,
      thirdSwitchToTimer
    };
  }
};
</script>

<style scoped>
 /*  -  */

#thirdtimeassistant.thirdcontainer {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  border-radius: 20px; 
  padding: 20px;
  box-shadow: 0 8px 32px rgba(31, 38, 135, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.18);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

/*  */
.thirdcontainer::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg, 
    rgba(255, 36, 36, 0.329) 0%, 
    rgba(156, 250, 33, 0.436) 25%, 
    rgba(35, 255, 185, 0.47) 50%, 
    rgba(92, 61, 244, 0.381) 75%, 
    rgba(255, 8, 111, 0.406) 100%
  );
  z-index: -1;
  transform: rotate(0deg);
  animation: flowBackground 15s linear infinite;
}

@keyframes flowBackground {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/*  */
#thirdtoprow {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 15px;
}

.thirtopaudio {
  width: 22%;
  height: 70%;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.thirtopaudio:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  background-color: #5d5d5dbf;
}

.thirtopaudio:active {
  transform: translateY(1px);
}

/*  */
#thirdmain {
  display: flex;
  width: 100%;
  height: 80%;
  box-sizing: border-box;
  gap: 15px;
}

/*  */
#thirdleftbar {
  width: 22%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 15px;
  box-sizing: border-box;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  overflow-y: auto;
}

.thirdctrlbtn {
  width: 100%;
  padding: 12px 5px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.thirdctrlbtn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateX(5px);
  background-color: #38383898;
}

/*  */
#thirdrightarea {
  width: 78%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 20px;
}

/*  - 3D */
#thirdtimedisplaywrap {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  box-sizing: border-box;
}

#thirdtimedisplay {
  width: 95%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 120px;
  border-radius: 15px;
  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.2);
  color: white;
  text-shadow: 3px 3px 0 #0c0c0c, 
              6px 6px 0 rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0.3),
              0 10px 20px rgba(0, 0, 0, 0.2);
  font-weight: 660;
  letter-spacing: 2px;
}

/*  */
#thirdprogresswrap {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 15px;
  position: relative;
}

.thirdprogressbar {
  width: 90%;
  height: 30%;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.3);
}

.thirdprogressinner {
  height: 100%;
  width: 0%;
  transition: width 0.3s linear, background-color 0.3s linear;
  border-radius: 10px;
}

/*  */
.thirdmarkerswrap {
  width: 90%;
  height: 10%;
  position: relative;
  margin-top: 10px;
}

.thirdmarker {
  position: absolute;
  top: 0%;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  z-index: 2;
}

/*  */
#thirdcontrols {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: center;
  gap: 20px;
  align-items: center;
}

.thirdactionbtn {
  width: 25%;
  padding: 12px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  font-size: 1.1rem;
  font-weight: bold;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
}

.thirdactionbtn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-5px);
  background-color: #59595993;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
}

/*  */
#thirdreliefview {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 10px;
}

.thirdreliefgrid {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  margin-bottom: 15px;
}

.thirdreliefcard {
  box-sizing: border-box;
  padding: 15px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.thirdreliefcard:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  background: rgba(255, 255, 255, 0.15);
}

.thirdreliefimg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: 10px;
  border-radius: 10px;
}

.thirdreliefbody {
  width: 100%;
  text-align: center;
}

.thirdrelieftitle {
  color: white;
  margin-bottom: 10px;
  font-size: 1.2rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.thirdreliefdesc {
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 10px;
  font-size: 0.9rem;
}

.thirdrelieflink {
  color: #a3d9ff;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}

.thirdrelieflink:hover {
  color: #7bcbff;
  text-decoration: underline;
}

/*  */
.thirdbackbtnwrap {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: auto;
}

/*  */
@media (max-width: 900px) {
  #thirdmain {
    flex-direction: column;
  }
  
  #thirdleftbar, #thirdrightarea {
    width: 100%;
  }
  
  #thirdleftbar {
    height: auto;
    max-height: 200px;
    flex-direction: row;
    flex-wrap: wrap;
    overflow-x: auto;
  }
  
  .thirdctrlbtn {
    width: 48%;
    padding: 8px 5px;
    font-size: 0.9rem;
  }
  
  .thirdreliefgrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .thirdreliefgrid {
    grid-template-columns: 1fr;
  }
  
  #thirdtimedisplay {
    font-size: 3.5rem;
  }
  
  .thirtopaudio {
    font-size: 1rem;
    padding: 5px;
  }
  
  .thirdactionbtn {
    width: 30%;
    font-size: 0.9rem;
  }
}
</style>