<template>
  <div id="secondresult">
    <canvas ref="chartCanvas"></canvas>
    <br><hr>
    <div id="secondpicsay" v-if="resultText">
      
      <h1>{{ resultText }}</h1>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted, nextTick } from 'vue'
import { Chart } from 'chart.js/auto'
import { eventBus } from '@/eventbus'
export default {
  props:['result'],
  setup(props) {
    const nowaday=new Date()
    const resultText = ref('')
    const labels = ["", "", "", "", "", "", ""]
    const textdatachange={
      '':0,
      '':1,
      '':2,
      '':3,
      '':4,
      '':5,
      '':6
    }
    var picdata = [
        props.result.data.joy,
        props.result.data.sadness,
        props.result.data.anger,
        props.result.data.surprise,
        props.result.data.disgust,
        props.result.data.fear,
        props.result.data.others
      ]
      picdata.forEach((el,index)=>{
        picdata[index]=Number(picdata[index])
      })
    function mainmood(ans){
      var mainre=""
      var mainrenum=0
      ans.forEach((el,index)=>{
        if(ans[index]>mainrenum)
        {
          mainre=labels[index]
          mainrenum=ans[index]
        }
      })
      console.log("res",mainre)
      return mainre
    }
    resultText.value = mainmood(picdata)
    eventBus.emit('changehistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:0,time:1,action:`<p class=actionmain><strong>${resultText.value}</strong></p>`})
    
    const chartCanvas = ref(null)
    let chartInstance = null
    

    
    async function showtextre(res) {
      await nextTick()

      if (!chartCanvas.value) {
        console.error('canvas ')
        return
      }
      if (chartInstance) {
        chartInstance.destroy()
      }

      

      const ctx = chartCanvas.value.getContext('2d')
      chartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels,
          datasets: [{
            label: '',
            data: picdata,
            backgroundColor: [
              'rgb(0,255,127)',
              'rgb(0,255,255)',
              'rgb(220,20,60)',
              'rgb(255,215,0)',
              'rgb(128,128,0)',
              'rgb(25,25,112)',
              'rgb(138,43,226)'
            ],
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            y: {
              beginAtZero: true,
              max: 100,
              ticks: {
                callback: function (value) {
                  return value.toFixed(0) + '%'
                }
              }
            }
          }
        }
      })
    //   var result={
    //     'joy': '',
    //     'sadness': '',
    //     'anger': '',
    //     'fear': '',
    //     'disgust': '',
    //     'surprise': '',
    //     'others': ''
    // }
    
    // console.log("",textdatachange[resultText.value.trim()],"",textdatachange,'',resultText.value.trim())
    eventBus.emit('changemoodhistory',{year:nowaday.getFullYear(),month:nowaday.getMonth(),day:nowaday.getDate(),where:textdatachange[resultText.value.trim()],time:1})
    }
    eventBus.emit('actionhistory',{action:`<p class=actionmain><strong>${resultText.value}</strong></p>`})
    showtextre(props.result)
    

    onMounted(() => {

    })

    onUnmounted(() => {
      if (chartInstance) {
        chartInstance.destroy()
      }
    })

    return {
      resultText,
      chartCanvas
    }
  }
}
</script>

<style scoped>

#secondresult{
  width: 100%;
  height: 100%;
  background-color: #ffffff82;
}
#secondresult:hover{
  background-color: #ffffffc6;
}
#secondpicsay{
  width: 90%;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-family: 'KaiTi';
  margin:0 auto;
}
</style>